.CurrencySelect {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.CurrencySelect__icon {
  margin-right: 8px;
}

.CurrencySelect__text {
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
}

.CurrencySelect__select {
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  outline: #000000;
}

.CurrencySelect__arrow {
  margin-left: 4px;
}

.CurrencySelect--disabled {
  cursor: default;
  .CurrencySelect__arrow {
    display: none;
  }
}

.CurrencySelect__container {
  width: fit-content;
  position: relative;
}

.CurrencySelect__button {
  display: flex;
  align-items: center;
}

.CurrencySelect__list {
  position: absolute;
  z-index: 10;
  width: 100%;
  left: 0;
  border: 1px solid rgba(34, 60, 80, 0.5);
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  padding: 8px;
}

.CurrencySelect__item {
  display: flex;
  align-items: center;

  &:not(:first-child) {
    padding-top: 4px;
  }
  &:not(:last-child) {
    padding-bottom: 4px;
    border-bottom: 1px solid rgba(34, 60, 80, 0.2);
  }
}
